@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
html {
  font-size: 100%;
}

* {
box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
  margin: 0;
  text-align: left;
  font-family: "Roboto", sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header h1 {
  font-size: 3rem;
  margin: 0;
}

header h2 {
  font-size: 2.5rem;
  margin: 0;
}

header h3 {
  font-size: 1.5rem;
  margin: 0;
}

header h4 {
  font-size: 1rem;
  margin: 0;
}

button {
  border: none;
  background-color: #039be5;
  color: #ffffff;
  padding: 0.625rem 2rem;
  margin-top: 1rem;
  font-size: 1.25rem;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}

button:hover {
  background-color: #03a9f4;
}

.wrapper {
  background-color: #ffffff;
  color: #000000;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

header.primary {
  background-color: #212121;
  color: #ffffff;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.26);
  width: 100%;
  margin: 0 auto;
  padding: 1rem 3rem;
  position: fixed;
  top: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

header.primary nav {
  display: grid;
  grid-template-areas: 'heading nav-buttons';
}

header.primary span.heading {
  font-size: 1.5rem;
  margin: auto 0rem;
  grid-area: heading;
  justify-self: start;
}

header.primary span.heading img {
  height: 3rem;
}

header.primary a.nav-link {
  color: #ffffff;
}

a.nav-link, a {
  text-decoration: none;
}

.nav-buttons {
  margin: auto 0rem;
  list-style: none;
  grid-area: nav-buttons;
  justify-self: end;
}

.nav-buttons li {
  display: inline-block;
  margin: 0.5em 1.25em;
}

.nav-buttons li a.nav-link {
  color: #ffffff;
  font-size: 1.25rem;
  opacity: 0.8;
  transition: 0.3s ease-in-out;
}

.nav-buttons li a.nav-link:hover {
  opacity: 1;
}

main.primary {
  padding: 7rem;
  height: 100vh;
}

@media only screen and (max-width: 720px) {
  main.primary {
    padding: 7rem 4rem;
  }

  header h1 {
    font-size: 2rem;
  }
  
  header h2 {
    font-size: 1.5rem;
  }
  
  header h3 {
    font-size: 1.25rem;
  }
  
  header h4 {
    font-size: 1rem;
  }
}
.stories-container {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.story-card {
  border-radius: 0.25rem;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
  background-color: #303030;
  color: #f5f5f5;
  height: 35rem;
  padding: 2rem;
  transition: 0.25s ease-in-out;
}

.story-card img {
  width: 80%;
  margin: 0.5rem auto;
}

.story-card:hover {
  -webkit-transform: scale(1.05) translate3d(0px, -0.5px, 0px);
          transform: scale(1.05) translate3d(0px, -0.5px, 0px);;
  box-shadow: 0px 19px 43px rgba(0, 0, 0, 0.15);
}

@media only screen and (max-width: 1440px) {
  .stories-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media only screen and (max-width: 1080px) {
  .stories-container {
    grid-template-columns: 1fr 1fr;
    height: 30rem;
  }
}

@media only screen and (max-width: 720px) {
  .stories-container {
    grid-template-columns: 1fr;
    height: 20rem;
  }
}
.story header {
  margin-bottom: 1rem;
}

.post {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  -webkit-align-items: center;
          align-items: center;
}

.post-content {
  margin: 0.5rem 0rem;
  margin-left: -3.5rem;
  display: grid;
  grid-template-columns: 3.5rem 1fr;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

.post-content .heart-container {
  font-size: 1.5rem;
  font-weight: normal;
}

.post-content svg.heart-button {
  width: 1.75rem;
  height: 1.5rem;
  margin-top: 0.1rem;
  stroke: #000000;
  stroke-width: 1px;
  stroke-linejoin: round;
  fill: rgba(255, 255, 255, 1);
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

.post-content svg.heart-button.active {
  stroke: rgba(244, 67, 54, 1);
  fill: rgba(244, 67, 54, 1);
  cursor: pointer;
}

.post-text {
  font-weight: normal;
  transition: 0.1s ease-in-out;
}

.post-text:hover {
  font-weight: bold;
}

.post-info {
  margin: 0.5rem 0rem;
  opacity: 0.0;
  transition: 0.1s ease-in-out;
}

.post-info.active {
  opacity: 1.0;
}

.post-section {
  border-top: 1px solid #212121;
  background-color: #ffffff;
  width: 100%;
  height: 25%;
  position: fixed;
  left: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.post-section form {
  width: 100%;
}

.post-section textarea {
  width: 80%;
  height: 5rem;
  margin: auto;
  padding: 0.5rem;
  display: block;
  font-family: "Roboto", "sans-serif";
  resize: none;
}

.post-section .error-message {
  color: #f44336;
  display: block;
  margin-bottom: 0.5rem;
}

.login-section {
  width: 100%;
  text-align: center;
}
